<template>
    <div class="CategoryPop" v-if="param.show">
        <div class="mask"></div>
        <div class="popContent">
            <div class="closeIcon" @click="close"></div>
            <div class="panels" v-if="param.type == 'add'">
                <div class="title">新增类目</div>
                <div class="content">
                    <div class="inputArea clearFix">
                        <div class="radio" :class="{checked: param.root}" @click="setRoot" :data-root="1"></div>
                        <div class="text">新增为一级类目</div>
                    </div>
                    <div class="inputArea clearFix">
                        <div class="radio" :class="{checked: !param.root}" @click="setRoot" :data-root="0"></div>
                        <div class="text">上级类目</div>
                        <div class="input">
                            <input v-model="parentId" placeholder="请输入上级类目编号" :disabled="param.root ? true : false"/>
                        </div>
                    </div>
                    <div class="inputArea clearFix">
                        <div class="blank"></div>
                        <div class="text">类目名称</div>
                        <div class="input">
                            <input v-model="name" placeholder="请输入类目名称"/>
                        </div>
                    </div>
                    <div class="inputArea clearFix">
                        <div class="text pl80">
                            <div class="checkbox" :class="{checked: param.itemCategory}" @click="setItemCategory"></div>
                            <div class="text">设为品类</div>
                        </div>
                    </div>
                    <div class="buttonArea singleButtonArea">
                        <div class="button" @click="submit">确定</div>
                    </div>
                </div>
            </div>

            <div class="panels" v-if="param.type == 'rename'">
                <div class="title">类目重命名</div>
                <div class="content">
                    <div class="inputArea pb20 clearFix">
                        <div class="text">
                            <span>{{param.settingProperty.name}}</span>
                            改名为</div>
                        <div class="input sortInput">
                            <input v-model="name" placeholder="请输入类目名称"/>
                        </div>
                    </div>
                    <div class="buttonArea singleButtonArea">
                        <div class="button" @click="submit">确定</div>
                    </div>
                </div>
            </div>

            <div class="panels" v-if="param.type == 'move'">
                <div class="title">类目迁移</div>
                <div class="content">
                    <div class="inputArea clearFix">
                        <div class="radio" :class="{checked: param.root}" @click="setRoot" :data-root="1"></div>
                        <div class="text">
                            提升为一级类目（下级类目顺应提升）
                        </div>
                    </div>
                    <div class="inputArea pb20 clearFix">
                        <div class="radio" :class="{checked: !param.root}" @click="setRoot" :data-root="0"></div>
                        <div class="text">迁移到此类目下</div>
                        <div class="input sortInput">
                            <input v-model="parentId" placeholder="请输入类目编号" :disabled="param.root ? true : false"/>
                        </div>
                    </div>
                    <div class="buttonArea singleButtonArea">
                        <div class="button" @click="submit">确定</div>
                    </div>
                </div>
            </div>

            <div class="panels" v-if="param.type == 'remove'">
                <div class="title">类目作废</div>
                <div class="content">
                    <div class="description">
                        <span>{{param.settingProperty.name}}</span> 作废后，商户无法发布<span>{{param.settingProperty.name}}</span>类目的商品。已审核通过和销售中的商品不受影响。
                    </div>
                    <div class="buttonArea singleButtonArea">
                        <div class="button" @click="submit">确定</div>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
	import './index.less';
    import Index from './index.js';
    export default Index;
</script>


