import ajax from 'fcbox/utils/ajax';

module.exports = {
    add(store, param) {
        const { state } = store;
        setTimeout(() => {
            state.treeParam.data.unshift({
                id: Math.random(), //后端返回
                parentId: param.parentId,
                name: param.name
            });
            state.treeParam.key = Math.random();
            state.popParam.show = false;
        });
    },

    move(store, param) {
        const { state, commit } = store;
        if (param.id == param.parentId) {
            commit('Alert/show', {
                text: '类目Id不能等于父ID'
            } , { root: true });
            return;
        }
        if (!param.root && !/\d/.test(param.parentId)) {
            commit('Alert/show', {
                text: '类目编号不能为空'
            } , { root: true });
            return;
        }
        if (param.propertyObj[param.id].parentId == param.parentId) {
            state.popParam.show = false;
            return;
        }
        if (!param.root && param.relativeObj[param.id]) {
            const { id, parentId, relativeObj, propertyObj } = param;
            const loopKeys = {};
            let loopId = id;
            loopKeys[loopId] = 0;
            let loopObj = relativeObj[loopId];
            let find = false;
            let num = 0;
            while(loopKeys[id] < relativeObj[id].length && !find) {
                loopKeys[loopId] = loopKeys[loopId] || 0;
                const pushId = loopObj[loopKeys[loopId]];
                if (pushId) {
                    if (pushId == parentId) {
                        find = true;
                        continue;
                    }
                    if (!relativeObj[pushId]) {
                        loopKeys[loopId] ++;
                    }
                }
                if (relativeObj[pushId]) {
                    loopObj = relativeObj[pushId];
                    loopId = pushId;
                } else if (loopKeys[loopId] >= loopObj.length) {
                    loopId = propertyObj[loopId] ? propertyObj[loopId].parentId : id;
                    loopObj = relativeObj[loopId];
                    loopKeys[loopId] ++;
                }
                num++;
            }
            if (find) {
                commit('Alert/show', {
                    text: '类目不能迁移到自己的子类'
                } , { root: true });
                return;
            }
        }
        setTimeout(() => {
            const data = state.treeParam.data;
            for (var i in data) {
                if (data[i].id == param.id) {
                    data[i].parentId = param.parentId;
                }
            }
            state.treeParam.key = Math.random();
            state.popParam.show = false;
        });
    },

    rename(store, param) {
        const { state } = store;
        const data = state.treeParam.data;
        setTimeout(() => {
            for (var i in data) {
                if (data[i].id == param.id) {
                    data[i].name = param.name;
                }
            }
            state.treeParam.key = Math.random();
            state.popParam.show = false;
        });
    },

    remove(store, param) {
        const { state } = store;
        const data = state.treeParam.data;
        setTimeout(() => {
            for (var i in data) {
                if (data[i].id == param.id) {
                    data[i].remove = true;
                }
            }
            state.treeParam.key = Math.random();
            state.popParam.show = false;
        });
    },


};


