<template>
    <div class="Alert" v-if="show">
        <div class="mask"></div>
        <div class="popContent">
            <div class="closeIcon" @click="close"></div>
            <div class="panels">
                <div class="title">{{title}}</div>
                <div class="content">
                    <div class="description" :style="{textAlign: align || 'left'}">
                        {{text}}
                    </div>
                    <div class="buttonArea singleButtonArea">
                        <div class="button" @click="submit">{{buttonText}}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
	import './index.less';
    import Index from './index.js';
    export default Index;
</script>


