import Vue from 'vue';
import { mapState } from 'vuex';

import CategoryStore from './store';
import FcTree from 'fcbox/tree/index.vue';
import Alert from 'fcbox/alert/index.vue';
import CategoryPop from './pop/index.vue';

const Category =  {
    name: 'Category',
    data() {
		return {};
	},
    components: {
        FcTree,
        CategoryPop,
        Alert
    },
    computed: Object.assign ({
            
        }, mapState({
            title: (state) => state.Category.title,
            treeParam: (state) => state.Category.treeParam,
            popParam: (state) => state.Category.popParam,
		})
	),
    created() {
        if (!this.$store.state.Category) {
            this.$store.registerModule('Category', CategoryStore);
        }
        document.title = this.title;
        console.log('created')
    },
    methods: {
        addCategory(e) {
            this.$store.commit('setValue', {
                key: 'Category.popParam',
                value: Object.assign(this.popParam, {
                    type: 'add',
                    show: true,
                    parentId: 0,
                    name: '',
                    root: true,
                })
            });
        },
        treeCallback(param) {
            switch (param.type) {
                case 'add':
                    this.$store.commit('setValue', {
                        key: 'Category.popParam',
                        value: Object.assign(this.popParam, {
                            type: 'add',
                            show: true,
                            parentId: param.settingId,
                            name: '',
                            root: false,
                        })
                    });
                    break;
                case 'rename':
                    this.$store.commit('setValue', {
                        key: 'Category.popParam',
                        value: Object.assign(this.popParam, {
                            type: 'rename',
                            show: true,
                            name: '',
                            settingProperty: param.settingProperty,
                            id: param.settingId
                        })
                    });
                    break;
                case 'move':
                    this.$store.commit('setValue', {
                        key: 'Category.popParam',
                        value: Object.assign(this.popParam, {
                            type: 'move',
                            show: true,
                            root: false,
                            parentId: '',
                            id: param.settingId,
                            relativeObj: param.relativeObj,
                            propertyObj: param.propertyObj,
                        })
                    });
                    break;
                case 'remove':
                    this.$store.commit('setValue', {
                        key: 'Category.popParam',
                        value: Object.assign(this.popParam, {
                            type: 'remove',
                            show: true,
                            id: param.settingId,
                            settingProperty: param.settingProperty,
                        })
                    });
                    break;
            }
        },
        popCallback(param) {
            const { dispatch, commit } = this.$store;
            switch (param.type) {
                case 'add':
                    if (!/\S/.test(param.name)) {
                        commit('Alert/show', {
                            text: '类目名不能为空'
                        });
                        return;
                    }
                    if (!param.root && !/\d/.test(param.parentId)) {
                        commit('Alert/show', {
                            text: '上级类目编号不能为空'
                        });
                        return;
                    }
                    dispatch('Category/add', param);
                    break;
                case 'move':
                    dispatch('Category/move', param);
                    break;
                case 'rename':
                    if (!/\S/.test(param.name)) {
                        commit('Alert/show', {
                            text: '类目名不能为空'
                        });
                        return;
                    }
                    dispatch('Category/rename', param);
                    break;
                case 'remove':
                    dispatch('Category/remove', param);
                    break;
            }
        },
    }
}

export default Category;


